<template>
  <div class="wrapper">
    <div v-for="article in articles" :key="article.id" @click="clickArticle(article.id)">
      <div style="width: 95%;margin: auto;border-radius: 5px;" class="aui-margin-b-15 white-bg">
        <div style="position: relative;">
          <img :src="article.img + '?imageView2/1/w/400/h/200/interlace/1'"
               style="border-top-left-radius: 5px;border-top-right-radius: 5px;height: 195px;width: 100%;"/>
          <div style="position: absolute;top: 40px;left: 0px;">
            <img src="../../../static/img/black_mask.png" style="width: 240px;height: 30px;opacity: 0.6;"/>
          </div>
          <div style="position: absolute;top: 40px;left: 0px;">
            <div class="text-oneline aui-text-white">
            <span class="aui-margin-l-10 aui-font-size-14"
                  style="line-height: 30px;height: 30px;">{{article.name}}</span>
            </div>
          </div>
        </div>
        <!--下方图片-->
        <div style="height: 50px;border-bottom-left-radius: 5px;border-bottom-right-radius: 5px;"
             class="white-bg aui-padded-t-5">
          <div class="aui-row-padded">
            <div class="aui-col-xs-6">
              <div>
                <img :src="article.user.avatar"
                     onerror="this.style.display='none'"
                     style="width: 35px;height: 35px;border-radius: 50%;display: inline-block;"
                     class="aui-margin-l-10 aui-pull-left aui-margin-r-10"/>
                <span class="text-oneline aui-font-size-14 aui-margin-l-10" style="height: 30px;line-height: 30px;">
                  {{article.user.nick_name}}
                </span>
              </div>
            </div>
            <div class="aui-col-xs-6">
              <!--相关数据及图表-->
              <div class="aui-flex-col aui-flex-middle aui-margin-t-10 aui-pull-right">
                <img src="../../../static/img/show_n.png" class="tab-icon-16 aui-margin-r-5"
                     style="display: inline-block;"/>
                <span class="font-size-10 text-grey aui-margin-r-10"
                      style="line-height: 16px;">{{article.show_num}}</span>
                <img src="../../../static/img/favor_n.png" class="tab-icon-16 aui-margin-r-5"
                     style="display: inline-block;"/>
                <span class="font-size-10 text-grey aui-margin-r-10" style="height: 12px;line-height: 12px;">{{article.zan_num}}</span>
                <img src="../../../static/img/comment_n.png" class="tab-icon-16 aui-margin-r-5"
                     style="display: inline-block;"/>
                <span class="font-size-10 text-grey aui-margin-r-15" style="height: 12px;line-height: 12px;">{{article.comm_num}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">

  import {jumpToArticle} from '../../assets/js/common'

  export default {
    name: 'articles_list',
    data() {
      return {}
    },
    props: {
      articles: {      //传入作品列表
        type: Array,
        require: true
      }
    },
    methods: {
      //点击作品
      clickArticle: function (article_id) {
        jumpToArticle({article_id: article_id, router: this.$router});
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



