<template>
  <div class="wrapper">
    <!--头部-->
    <div v-if="showPage_flag">
      <div style="position: relative;">
        <!--背景图片-->
        <img :src="ad.img+'?imageView2/1/w/600/h/140/interlace/1'" style="width: 100%;height: 90px;"/>
        <!--照相机图标-->
        <img src="../../static/img/camera_white.png"
             style="top: 10px;right: 10px;position: absolute;width: 30px;height: 30px;"/>
        <!--头像及昵称-->
        <div class="aui-row" style="margin-top: -35px;">
          <div class="aui-col-xs-3">
            <img :src="user.avatar+'?imageView2/1/w/200/h/200/interlace/1'"
                 style="width: 55px;height: 55px;display: inline-block;"
                 class="aui-margin-l-15"/>
          </div>
          <div class="aui-col-xs-9">
            <span class="aui-font-size-16 aui-text-white"
                  style="line-height: 40px;">{{user.nick_name}}</span>
          </div>
        </div>
      </div>
    </div>

    <mescroll-vue ref="mescroll" :up="mescrollUp" :down="mescrollDown" @init="mescrollInit">
      <div style="height: 10px;"></div>
      <!--作品列表-->
      <v-article-list v-bind:articles="articles"></v-article-list>
    </mescroll-vue>

    <v-tabbar-index/>
  </div>
</template>

<script>

  // 微信jssdk
  import wx from 'weixin-js-sdk'

  // 引入mescroll的vue组件
  import MescrollVue from 'mescroll.js/mescroll.vue'

  import tab_index from '../components/tab/tab_index'
  import article_list from '../components/article/article_list'

  import {store} from '../store/index';   //vuex封装，用于记录页面跳转位置

  import {consoledebug, DEFAULT_SHARE_TITLE, DEFAULT_SHARE_DESC, DEFAULT_SHARE_IMGURL} from '../../src/assets/js/common'

  //获取作品列表的参数
  var article_param = {
    'recomm_flag': '1',    //只获取推荐的作品
    'busi_name': 'ysb'     //获取艺术榜的作品
  }


  var self = null;    //在create方法中初始化为this

  export default {
    components: {
      'v-tabbar-index': tab_index,
      'v-article-list': article_list,
      MescrollVue,
    },
    data() {
      return {
        showPage_flag: false,   //展示页面的标识
        ad: {},    //活动广告图对象
        articles: [],    //作品列表
        user: {},    //用户信息
        mescroll: null, // mescroll实例对象
        mescrollUp: {
          use: true, //是否初始化上拉加载; 默认true
          auto: true, //是否在初始化时以上拉加载的方式自动加载第一页数据; 默认false
          callback: this.upCallback, // 上拉回调,此处可简写; 相当于 callback: function (page, mescroll) { getListData(page); }
        },
        mescrollDown: {
          use: true, //是否初始化上拉加载; 默认true
          auto: false, //是否在初始化时以上拉加载的方式自动加载第一页数据; 默认false
          callback: this.downCallback, // 上拉回调,此处可简写; 相当于 callback: function (page, mescroll) { getListData(page); }
        },
      }
    },
    // 进入路由时,恢复列表状态
    beforeRouteEnter(to, from, next) {  // 如果没有配置回到顶部按钮或isBounce,则beforeRouteEnter不用写
      // console.log("before RouteEnter");
      next(vm => {
        if (vm.mescroll) {
          // 恢复到之前设置的isBounce状态
          if (vm.mescroll.lastBounce != null) vm.mescroll.setBounce(vm.mescroll.lastBounce)
          // 滚动到之前列表的位置 (注意:路由使用keep-alive才生效)
          if (vm.mescroll.lastScrollTop) {
            // console.log("vm.mescroll:" + vm.mescroll.lastScrollTop);
            let scrollY = vm.mescroll.lastScrollTop;
            vm.$nextTick(() => {
              vm.mescroll.setScrollTop(scrollY)
            })
          }
        }
      })
    },
    // 离开路由时,记录列表状态
    beforeRouteLeave(to, from, next) {  // 如果没有配置回到顶部按钮或isBounce,则beforeRouteLeave不用写
      // console.log("before RouteLeave");
      if (this.mescroll) {
        this.mescroll.lastBounce = this.mescroll.optUp.isBounce// 记录当前是否禁止ios回弹
        this.mescroll.setBounce(true) // 允许bounce
        this.mescroll.lastScrollTop = this.mescroll.getScrollTop()// 记录当前滚动条的位置
        this.mescroll.hideTopBtn(0)// 隐藏回到顶部按钮,无渐隐动画
      }
      next()
    },
    created() {
      self = this;    //使用self来代替this，避免this无效
    },
    mounted() {
      this.init();
      window.addEventListener('scroll', this.handleScroll)
      var wxConfig_param = {
        "url": window.location.href.split('#')[0],
        "jsApiList": "onMenuShareTimeline,onMenuShareAppMessage"
      }
      self.api.wechat_wxConfig(wxConfig_param).then((res) => {
        self.common.consoledebug.log("wechat_wxConfig res:" + JSON.stringify(res));
        if (res.data.result) {
          var wxConfig = res.data.ret;
          wx.config(wxConfig);
        }
      }).catch((err) => {

      });
      //微信初始化配置
      wx.ready(function () {
        self.common.consoledebug.log("wx.ready ok");
        //分享给好友
        wx.onMenuShareTimeline({
          title: DEFAULT_SHARE_TITLE, // 分享标题
          desc: DEFAULT_SHARE_DESC, // 分享描述
          link: window.location.href.split('#')[0] + "#" + window.location.href.split('#')[1], // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl: DEFAULT_SHARE_IMGURL, // 分享图标
          success: function () {
            // 设置成功
          }
        });
        //分享至朋友圈
        wx.onMenuShareAppMessage({
          title: DEFAULT_SHARE_TITLE, // 分享标题
          desc: DEFAULT_SHARE_DESC, // 分享描述
          link: window.location.href.split('#')[0] + "#" + window.location.href.split('#')[1], // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl: DEFAULT_SHARE_IMGURL, // 分享图标
          success: function () {
            // 设置成功
          }
        });
      });
    },
    methods: {
      // mescroll组件初始化的回调,可获取到mescroll对象
      mescrollInit(mescroll) {
        this.mescroll = mescroll
      },
      handleScroll() {    //监听滚动事件
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        // console.log(scrollTop)
      },
      //初始化页面
      init: function () {
        //获取首页轮播图
        self.api.ad_getListByCon({position: '1'}).then((res) => {
          var msgObj = res.data.ret;
          if (res.data.result && msgObj.length > 0) {
            this.ad = res.data.ret[0];  //获取第一张轮播图
            this.showPage_flag = true;
          }
        }).catch((err) => {
          self.common.consoledebug.log("err:" + JSON.stringify(err));
          mescroll.endErr()
        });
        //获取用户信息
        console.log("+++++++++++++++++++++++++++++++++user_id:" + window.localStorage.getItem("user_id"));
        self.api.user_getById({"id": window.localStorage.getItem("user_id")}).then((res) => {
          var msgObj = res.data.ret;
          self.common.consoledebug.log("user_getById msgObj:" + JSON.stringify(msgObj))
          this.user = msgObj;
        }).catch((err) => {
        });
      },
      //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
      upCallback(page) {
        self.common.consoledebug.log("upCallback page:" + JSON.stringify(page));
        article_param.page = page.num;
        self.api.article_getListByCon(article_param).then((res) => {
          if (res.data.result) {
            var msgObj = res.data.ret.data;
            for (var i = 0; i < msgObj.length; i++) {
              //如果不在现有的作品列表中，则进行加入
              if (!self.common.isDomInArr(msgObj[i].id, this.articles)) {
                this.articles.push(msgObj[i]);
              }
            }
            // 数据渲染成功后,隐藏下拉刷新的状态
            this.$nextTick(() => {
              this.mescroll.endSuccess(msgObj.length)
            })
          }
        }).catch((err) => {
          self.common.consoledebug.log("err:" + JSON.stringify(err));
        });
      },
      downCallback() {
        self.common.consoledebug.log("downCallback page");
        self.api.article_getListByRand({}).then((res) => {
          if (res.data.result) {
            var msgObj = res.data.ret;
            for (var i = 0; i < msgObj.length; i++) {
              //如果不在现有的作品列表中，则进行加入
              if (!self.common.isDomInArr(msgObj[i].id, this.articles)) {
                this.articles.unshift(msgObj[i]);
              }
            }
            // 数据渲染成功后,隐藏下拉刷新的状态
            this.$nextTick(() => {
              this.mescroll.endSuccess(msgObj.length)
            })
          }
        }).catch((err) => {
          self.common.consoledebug.log("err:" + JSON.stringify(err));
        });

        this.mescroll.endSuccess();
      },
    }
  }
</script>

<style scoped>
  .mescroll {
    position: fixed;
    top: 110px;
    bottom: 0;
    height: auto;
  }
</style>



